html, body {
    scroll-behavior: smooth;
    scroll-padding-top: 100px;
}

.toc-fades {
    position: relative;

    &::before {
        content: "";
        opacity: 1;
        transition: opacity 0.2s;
        pointer-events: none;
        position: absolute;
        left: 0; right: 0; top: 0;
        height: 3.5em;
        z-index: 2;
        background: linear-gradient(to top, rgba(255,255,255,0), $light-background);
        .dark-theme & {
            background: linear-gradient(to top, rgba(37,38,39,0), $dark-background);
        }
    }
    &.at-top::before {
        opacity: 0;
    }
    &::after {
        content: "";
        pointer-events: none;
        position: absolute;
        left: 0; right: 0; bottom: 0;
        height: 3.5em;
        z-index: 2;
        background: linear-gradient(to bottom, rgba(255,255,255,0), $light-background);
        .dark-theme & {
            background: linear-gradient(to bottom, rgba(37,38,39,0), $dark-background);
        }
    }
    &.at-bottom::after {
        opacity: 0;
    }
}

.toc {
    position: relative;
    max-height: 70vh;
    overflow-y: auto;

    // Hide scrollbar for WebKit browsers (Chrome, Safari, Edge)
    &::-webkit-scrollbar {
        width: 0;
        background: transparent;
    }
    // Hide scrollbar for Firefox
    scrollbar-width: none;
    // Hide scrollbar for IE/Edge (legacy)
    -ms-overflow-style: none;
}

.toc a {
    text-decoration: none;
    text-align: left;
    display: block;
    padding: .125rem 0;
    transition: all 50ms ease-in-out;
    margin-bottom: 8px;
    line-height: 20px;
}

// .toc a:before {
//     content:"• ";
// }

.toc a:hover,
.toc a:focus {
    color: #666;
}

.active {
    color: #b1b1b1;
    .dark-theme & {
        color: #e7e7e7;
    } 
}

.toc-number {
    color: $light-color-heading-number;
    margin-right: 0.1em;
    font-variant-numeric: tabular-nums;
    font-weight: 500;
    .dark-theme & {
      color: $dark-color-heading-number;
    }
  }

* {
    box-sizing: border-box;
}

ul, ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

li {
    margin-left: 1rem;
    margin-bottom: 4px;
}

// For non-mobile turn on post-wrapper
@media (min-width: 1580px) {
    .post-wrapper {
        display: grid;
        grid-template-columns: 750px 20em;
        max-width: 800px;
        width: 90%;
        margin: 0 auto;
    }
    // TODO: fix, when too many headers it won't scroll
    .post-wrapper > aside {
        margin-left: 50px;
        position: sticky;
        top: 7rem;
        align-self: start;
    }
}

@media (max-width: 1080px) {
    main {
        max-width: 100%;
    }

    .toc {
        display: none;
    }
    
    // for mobile devices flexbox does not like grid - need to turn off aligning
    .content {
        align-items: unset;
    }

    // why webdev must be so hackish???
    pre code {
        white-space: pre;
        white-space: -moz-pre-wrap;
        word-wrap: normal; // for Safari because break does not work properly
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }
   
}
